.paginado {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: baseline;
  align-content: stretch;
  margin-right: 12px;
  margin-bottom: 20px;
}

.number {
  background-color: #142f43;
  color: white;
  width: 30px;
  height: 10px;
  margin-right: 3px;
  margin-top: 15px;
  padding-bottom: 18px;
  border: 1px solid white;
  cursor: pointer;
  border-radius: 0.3vw;
}
.number:focus {
  background-color: white;
  color: #142f43;
}